<template>
    <div class='homepage'>
        <span>房间号：</span><input style="width:149px" type="text" v-model="rid" />
        <br/>
        <span>开始时间：</span><input type="date" v-model="beginTime"/>
        <br/>
        <span>结束时间：</span><input type="date" v-model="endTime"/>
        <br/>
        <input style="width:221px" type="button" value="查询" @click="query">
        <br/>
        <table>
            <tr>
                <th>房间号</th>
                <th>礼物名称</th>
                <th>礼物数量</th>
                <th>赠送者</th>
                <th>时间</th>
            </tr>
            <tr v-for="(item, index) in recordsList" :key="index">
                <td>{{ item.rid }}</td>
                <td><img :src="imgMap[item.gfnn]">{{ item.gfnn }}</td>
                <td>{{ item.gfnum }}</td>
                <td>{{ item.nn }}</td>
                <td>{{ String(item.timestamp).split(" ")[1] }}</td>
            </tr>
        </table>
        <div class="prevPage" @click="prevPage">上一页</div>{{currentPage}}<div class="nextPage" @click="nextPage">下一页</div>
    </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { getNowFormatDate } from "../common"
import { getLotteryRecord } from "../apis"
export default {
    components: {},
    setup() {
        const rid = ref("");
        const beginTime = ref("");
        const endTime = ref("");
        const recordsList = ref([]);
        const currentPage = ref(1);

        const imgMap = ref({
            "爱心寿司": "https://gfs-op.douyucdn.cn/dygift/2020/05/28/6c5cb58b130e3fc25fda561d6721f0d8.gif",
            "可乐鸡翅": "https://gfs-op.douyucdn.cn/dygift/2020/05/28/d92b73c7ddb533eb3c14120507774718.gif",
            "海鲜大餐": "https://gfs-op.douyucdn.cn/dygift/2020/05/29/40999cd5a538b70ce06f2e09d52c125f.gif",
            "鱼丸粗面": "https://gfs-op.douyucdn.cn/dygift/2020/05/28/8b8c9556f310b30a832ce826ab73fef4.gif",
            "满汉全席": "https://gfs-op.douyucdn.cn/dygift/2020/05/29/697e6c65c7eda6e7fd9d270d635fb278.gif",
            "麻辣烫": "https://gfs-op.douyucdn.cn/dygift/2020/05/28/d3ecf43396441d22b6d3db413dc208c4.gif"
        })


        const query = async () => {
            let ret = await getLotteryRecord(rid.value, beginTime.value + " 00:00:00", endTime.value + " 23:59:59", 1);
            if (ret.error == "0") {
                recordsList.value = ret.data;
            }
        }

        const prevPage = async () => {
            if (currentPage.value > 1) {
                currentPage.value--;
                let ret = await getLotteryRecord(rid.value, beginTime.value + " 00:00:00", endTime.value + " 23:59:59", currentPage.value);
                if (ret.error == "0") {
                    recordsList.value = ret.data;
                }
            }
        }

        const nextPage = async () => {
            currentPage.value++;
            let ret = await getLotteryRecord(rid.value, beginTime.value + " 00:00:00", endTime.value + " 23:59:59", currentPage.value);
            if (ret.error == "0") {
                recordsList.value = ret.data;
            }
        }

        onMounted(() => {
            beginTime.value = getNowFormatDate();
            endTime.value = getNowFormatDate();
        });
        return {
            rid, beginTime, endTime, currentPage, recordsList, imgMap,
            query, prevPage, nextPage
        }
    }
}


</script>

<style scoped>
.homepage table {
    border-collapse: collapse;
}

.homepage td,
.homepage th {
    border: 1px solid #cad9ea;
    color: #666;
    height: 20px;
    width: 130px;
    text-align: center;
}

.homepage thead th {
    background-color: #CCE8EB;
    width: 100px;
}

.homepage tr:nth-child(odd) {
    background: #fff;
}

.homepage tr:nth-child(even) {
    background: #F5FAFA;
}

.homepage img {
    width: 20px;
    height: 20px;
}

.prevPage, .nextPage{
    display: inline-block;
    width: 150px;
    text-align: center;
    margin: 10px;
    border: 1px solid black;
    cursor: pointer;
}

.prevPage:hover {
    background: gray;
}

.nextPage:hover {
    background: grey;
}
</style>>