import axios from "axios"

const getLotteryRecord = (rid, beginTime, endTime, page) => {
    return new Promise(resolve => {
        axios({
            method: "POST",
            url: "http://122.51.5.63:27998/douyu/lotteryRecord",
            responseType: "json",
            headers: {

            },
            data: `rid=${rid}&beginTime=${beginTime}&endTime=${endTime}&page=${page}`
        }).then((ret) => {
            resolve(ret.data);
        })
    })
}

export {
    getLotteryRecord
}